





import EventBus from '@/EventBus';
import FSPoint from '@/intern/FSPoint';

// eslint-disable-next-line
const OffWorker = require('worker-loader!@/workers/offscreen.worker.js');

import { Component, Vue, Watch } from 'vue-property-decorator';

@Component({})
export default class FS2DCanvas extends Vue{


  $refs!: {
    can: HTMLCanvasElement[];
  }

  private worker!: Worker;
  private point: FSPoint | null = new FSPoint();
  private pnts!: FSPoint[];
  private offscreen!: any;
  private pressure!: number;

  @Watch('point', {deep: true})
  private pointChange(n, o){
    this.pnts.push(n);
    const lineBuffer = 5;
    if(n && o && this.pnts.length > lineBuffer){
      if(typeof this.$refs.can[0].transferControlToOffscreen === "function"){
        this.worker.postMessage({prs: this.pressure, pnts: this.pnts.slice(-lineBuffer, -1)});
      }else{
        this.renderer([this.pnts[this.pnts.length - 4], this.pnts[this.pnts.length-1]]);
      }
    }
  }

  destroyed(){
    EventBus.$off('pMv');
  }

  private renderer(pnts){
    const ctx = this.$refs.can[0].getContext('2d') as CanvasRenderingContext2D;
    const p1 = new FSPoint(pnts[0].x, pnts[0].y);
    const p2 = new FSPoint(pnts[1].x, pnts[1].y);
    const dist = p1.to(p2).dist / 1

    ctx.beginPath();
    ctx.moveTo(p1.x + dist, p1.y + dist);
    // ctx.lineTo(p2.x, p2.y);
    ctx.lineTo(p2.x+dist, p2.y+dist);
    ctx.lineTo(p2.x-dist, p2.y-dist);
    ctx.lineTo(p1.x - dist, p1.y - dist);
    ctx.closePath();
    // ctx.stroke()
    ctx.fill()

  }

  mounted(){
    this.$refs.can.forEach(can => {
      can.width = window.innerWidth;
      can.height = window.innerHeight;
    });

    if(typeof this.$refs.can[0].transferControlToOffscreen === "function"){
      this.offscreen = this.$refs.can[0].transferControlToOffscreen();
      this.worker = OffWorker({type: module});
      this.worker.postMessage({canvas: this.offscreen}, [this.offscreen]);
      // console.log(this.worker)
    }

    EventBus.$on('pUp', (e) => {
      this.worker.postMessage({off: true});
    });

    EventBus.$on('pMv', (e) => {
      if(e.pressure){
        // this.point = new FSPoint(e.offsetX, e.offsetY);
        this.pressure = e.pressure;
        this.point = new FSPoint(Math.floor(e.clientX), Math.floor(e.clientY));
      } else {
        this.pnts = [];
        this.point = null;
      }
    });


    this.$nextTick(() => {
      //
    });
  }
